.yard-list{
    border-radius: 0.2rem;
    margin-bottom:0.1rem; 
    background: #fff;
    .yard-header{
        height: 2.4rem;
        display: flex;
        .yard-header-content:nth-of-type(1){
            border-right: 1px solid LavenderBlush;
        }
        .yard-header-content{
            padding: 0.1rem 0.2rem;
            border-bottom: 1px solid LavenderBlush;
            width: 50%;
            h3{
                font-weight: 700;
                font-size: .32rem;
                line-height: 0.44rem;
                display: inline-block;
                max-width: 100%;
                height: 0.44rem;
                background: linear-gradient(90deg, #45CAFF,#1471FB);
                background-clip: text;
                color: transparent;
            }
            span{
                height: 0.3rem;
                font-size: 0.14rem;
                line-height: 0.3rem;
                display: block;
            }
            .run{
                display: flex;
                justify-content: space-around;
                img{
                    width: 1.2rem;
                    height:1.2rem;
                    margin: 0.1rem 0 ;
                    border-radius: 0.1rem;

                }
            }
        }
    }
    .yard-content{
        height: 2.4rem;
        display: flex;
        padding:0  0.2rem;
        .yard-content-content:nth-of-type(4){border-right:none;}
        .yard-content-content{
            width: 25%;
            border-right:  1px solid LavenderBlush;
            h3{
                font-weight: 700;
                font-size: .32rem;
                line-height: 0.44rem;
                max-width: 100%;
                height: 0.44rem;
                color: #000000;
                text-align: center;
                margin-top:0.1rem; 
            }
            span{
                height: 0.3rem;
                font-size: 0.24rem;
                line-height: 0.3rem;
                display: block;
                text-align: center;
            }
            img{
                width: 1.2rem;
                height:1.2rem; 
                display: block;
                margin: auto;
            }
        }
    }
}
