.list{
    width: 100%;
    overflow: hidden;
    ul{
        width: 100%;
        display: flex !important;
        flex-wrap: wrap;
        li{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 20%;
            i{
                height: 0.8rem;
                width: 0.8rem;
                display: block;
                margin-top:0.2rem; 
                img{
                    width: 0.8rem;
                    height: 0.8rem;
                }
            }
            span{
                display: block;
                margin-top: .3rem;
                font-size: .2rem;
                color: #666;
            }
        }
    }
}
