.content-list {
  height: 100%;
  display: flex;
  aside {
    width: 1.7rem;
    overflow: auto;
    height: 100%;
    ul{
        border-right: 1px solid LavenderBlush;
        background:#fff;
        li{
            height: 0.92rem;
            display: flex;
            cursor: pointer;
            span{
                font-size: 0.24rem;
                line-height: 0.46rem;
                text-align: center;
                margin: 0.23rem 0.1rem;
                width:1.5rem;
                display: block;
                border-radius: 0.23rem;
            }
        }
        .active{
          span{
              background:#ff5000;
              color: #fff;
          }
      }
    }
  }
  .container {
    flex: 1;
    padding: 0.14rem;
    background: #fff;
    overflow: auto;
    .content-container:nth-of-type(1){
      .container-header{
        .container-header-right{
          display: block;
        }
      }
    }
    .content-container{
      .container-header{
        display: none;
        height: 0.48rem;
        display: flex;
        justify-content: space-between;
        .container-header-left{
          height: 0.48rem;
          h4{
            line-height: 0.48rem;
            font-size:0.24rem; 
          }
        }
        .container-header-right{
          display: none;
          height: 0.48rem;
          span{
            font-size: 0.24rem;
            line-height: 0.48rem;
            margin: 0 0.08rem;
          }
          i:nth-of-type(1){
            color:red;
            font-size: 0.28rem;
            vertical-align: middle;
          }
          i:nth-of-type(2){
            color:#C0C0C0;
            font-size: 0.24rem;
            line-height: 0.48rem;
          }
        }
      }
  }
    .contaniner-content{
      margin-top:0.18rem; 
      ul{
        display:flex;
        flex-wrap: wrap;
        width:100%;
        li{
          width: 33.3%;
          img{
            width: 1.4rem;
            height: 1.4rem;
            display: block;
            margin: auto;
          }
          span{
            margin-top:0.08rem; 
            height: 0.7rem;
            font-size: 0.24rem;
            display: block;
            text-align: center;
          }
        }
      }
    }
  }
}
