.goods-list{
    margin-top:0.2rem; 
    ul{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        li{
            position: relative;
            width: 48%;
            background: #fff;
            margin-bottom:4%;
            border-radius: 3%;
            img{
                border-top-left-radius: 3%;
                border-top-right-radius: 3%;
                width: 100%;
                height: 3.6rem;
            }
            p{
                margin:0.2rem 0.2rem 0.6rem 0.2rem;
                font-size: 0.24rem;
            }
            .goods-bottom{
                position: absolute;
                bottom: 0;
                display: flex;
                justify-content: space-between;
                width: 100%;
                padding:0 0.2rem;
                span{
                    color:#ff5000;
                    font-size:0.28rem; 
                }
                b{
                    font-size: 0.2rem;
                }
            }
        }
    }
}