.floor-list{
    margin: 0.2rem;
    background: #fff;
    border-radius: 0.1rem;
    overflow: hidden;
    .floor-header{
        height: 0.68rem;
        background: url('data:image/png;base64,');
        background-size: contain;
        display: flex;
        justify-content: space-between;
        .floor-height-left{
            display: flex;
            justify-content: left;
            .floor-img{
                height: 0.54rem;
                width: 1.38rem;
                background: url('data:image/png;base64,') no-repeat;
                background-size:contain; 
                margin: 0.16rem 0.12rem 0 0.16rem;
            }
            strong{
                display: block;
                font-size: 0.24rem;
                line-height:0.68rem;
                color: #232323;
            }
            .floor-time{
                vertical-align: middle;
                align-items: center;
                display: flex;
                margin-left:0.12rem; 
                span{
                    height: 0.32rem;
                    color: #232323;
                    font-size:0.24rem;
                    line-height:0.32rem; 
                }
                .hours{
                    width:0.36rem;
                    height:0.32rem;
                    border:1px solid #dfdfdf;
                    border-radius: 2px;
                    color: #232323;
                    text-align: center;
                    font-size:0.24rem;
                    line-height:0.32rem; 
                }
                .min{
                    width:0.36rem;
                    height:0.32rem;
                    border:1px solid #dfdfdf;
                    border-radius: 2px;
                    color: #232323;
                    text-align: center;
                    font-size:0.24rem;
                    line-height:0.32rem; 
                }
                .s{
                    width:0.36rem;
                    height:0.32rem;
                    border:1px solid #dfdfdf;
                    border-radius: 2px;
                    color: #232323;
                    text-align: center;
                    font-size:0.24rem;
                    line-height:0.32rem; 
                }
            }
        }
        .floor-height-right{
            a{
                padding: 0 0.44rem 0 0;
                display: block;
                color:#f23030;
                text-align: center;
                line-height: 0.68rem;
                position: relative;
                font-size:0.24rem; 
                i{
                    background: url('http://wq.360buyimg.com/wxsq_project/portal/m_jd_index/images/arrow_rt_2ccb8cd5.png') no-repeat;
                    background-size: cover;
                    display: inline-block;
                    height: 0.22rem;
                    width: 0.22rem;
                    position: absolute;
                    right: 0.16rem;
                    top:0.2rem;
                }
            }
        }
    }
    .floor-content{
        height: 2.4rem;
        ul{
            display: flex;
            height: 100%;
            padding: 0 0.1rem;
            width: 100%;
            overflow-x: scroll;
            li{
                height: 100%;
                width: 1.53rem;
                padding:0 0.1rem;
                img{
                    width: 1.32rem;
                    height: 1.32rem;
                }
                span{
                    display: block;
                    color: #e93b3d;
                    margin-top:0.2rem;
                    font-size: 0.3rem;
                    height:0.3rem;
                    line-height: 0.3rem;
                    text-align: center; 
                    font-family: JDZhengHT-EN-Bold;
                }
                del{
                    text-align: center;
                    display: block;
                    font-size: 0.24rem;
                    line-height: 0.24rem;
                    height: 0.24rem;
                    color: #999999;
                    margin: 0.08rem 0 0.22rem;
                }
                del::before{
                    content: '';
                    display: block;
                    height: 1px;
                    width: 58%;
                    background: #999999;
                    position: relative;
                    top:0.12rem;
                    left: 0.3rem;
                }
            }
        }
    }
}