.scrolllist {
  height: 0.6rem;
  padding: 0.14rem 0.2rem;
  background: #fff;
  margin-bottom:0.3rem; 
  .content-s {
    background: #fff;
    position: relative;
    height: 0.32rem;
    .scroll-img {
        position: absolute;
        left: 0;
        width: 1.5rem;
        img {
          height: 0.32rem;
        }
      }
    .scroll-list {
        padding: 0 0.84rem 0 1.5rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size:0.24rem;
        line-height: .32rem; 
        span{
            color: red;
            margin-right:0.2rem;
        }
    }
    .scroll-right {
        position: absolute;
        right: 0;
        top:0.01rem;
        width: 0.84rem;
        font-size: 0.24rem;
        line-height: 0.3rem;
        border-left:1px solid #cccccc;
        text-align: center;
    }
  }

}
