.box{
    .header{
        height:0.88rem;
        display: flex;
        border-bottom:.01rem solid LavenderBlush;
        input[type="text"]{
            width: 64%;
            margin: 0.14rem 3%;
            border: 1px solid #ccc;
            border-radius: 0.34rem;
            font-size: 0.24rem;
            line-height: 0.36rem;
            text-indent: 0.5rem;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            color: 	#C0C0C0;
            background: url('https://i.loli.net/2019/09/15/I6gujLVZfFnxDPw.png') no-repeat;
            background-size: 0.36rem;
            background-position: 0.12rem 0.11rem; 
        }
        .header-right{
            width: 15%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            padding:0.04rem 0; 
            color: 	#C0C0C0;
            span{
                font-size: 0.48rem;
                line-height: 0.5rem;
            }
        }
        .header-left{
            width: 15%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            padding:0.04rem 0; 
            color: 	#C0C0C0;
            span{
                font-size: 0.48rem;
                line-height: 0.5rem;
                margin-top:0.04rem; 
                opacity: 0.7;
            }
        }
    }
}