.daylist,.day {
    height: 4.8rem;
    margin-bottom:0.1rem;
    background: #fff;
    border-radius: 0.2rem;
    ul{
        padding: 0 0.2rem;
        display: flex;
        flex-wrap: wrap;
        li{
            width: 25%;
            margin: 0.2rem 0;
            h3{
                text-align: center;
                font-weight: 700;
                font-size: .32rem;
                line-height: 0.44rem;
                display: block;
                max-width: 100%;
                height: 0.44rem;
            }
            h3:nth-of-type(2n){
                background: linear-gradient(90deg, #2AD396,#85BB1F);
                background-clip: text;
                color: transparent;
            }
            h3:nth-of-type(2n+1){
                background: linear-gradient(90deg, #FF2A00,#FF00AF);
                background-clip: text;
                color: transparent;
            }

            span{
                display: block;
                text-align: center;
                font-size: 0.24rem;
            }
            img{
                width:1.2rem;
                height:1.2rem;
                display: block;
                margin: auto;
            }
        }
    }
}